import React, { useState, useEffect } from 'react';
import { Link } from 'react-scroll';
import { useLocation, useNavigate } from 'react-router-dom';
import Logo from '../../assets/Logo.webp';
import { IonIcon } from '@ionic/react';

const Navigation = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [activeLink, setActiveLink] = useState('home');
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY + 80;
      const sections = ['home', 'robofest', 'courses', 'why-robotics', 'workshops', 'contact'];

      sections.forEach(section => {
        const element = document.getElementById(section);
        if (element && scrollPosition >= element.offsetTop && scrollPosition < element.offsetTop + element.offsetHeight) {
          setActiveLink(section);
        }
      });
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  useEffect(() => {
    setActiveLink(location.hash.substring(1));
  }, [location]);

  const handleSoftwareClick = () => {
    setIsOpen(false);
    navigate('/software');
  };

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const offsetValue = -80;

  return (
    <nav className="bg-blue-900 p-4 flex justify-between items-center h-20 fixed w-full z-10">
      <div>
        <img src={Logo} alt="Logo" className="w-48 h-auto" />
      </div>
      <div className="hidden md:flex space-x-6 items-center">
        <NavLink to="home" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Home</NavLink>
        <NavLink to="robofest" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>RoboFest 2024</NavLink>
        <NavLink to="courses" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Our Courses</NavLink>
        <NavLink to="why-robotics" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Why Robotics</NavLink>
        <NavLink to="workshops" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Our Workshops</NavLink>
        <button onClick={handleSoftwareClick} className="text-white py-2 px-4 cursor-pointer font-sans">Tech-Champions</button>
        <NavLink to="contact" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>
          <button className="bg-green-600 text-white px-6 py-2 rounded-full text-lg">
            Contact Us
          </button>
        </NavLink>
      </div>
      <div className="md:hidden">
        <button onClick={toggleMenu} className="text-white">
          <IonIcon icon={isOpen ? 'close' : 'menu'} style={{ color: 'white' }} />
        </button>
        {isOpen && (
          <div className="absolute top-20 left-0 w-full bg-blue-800 flex flex-col items-center">
            <NavLink to="home" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Home</NavLink>
            <NavLink to="robofest" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>RoboFest 2024</NavLink>
            <NavLink to="courses" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Our Courses</NavLink>
            <NavLink to="why-robotics" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Why Robotics</NavLink>
            <NavLink to="workshops" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>Our Workshops</NavLink>
            <button onClick={handleSoftwareClick} className="text-white py-2 px-4 cursor-pointer font-sans text-lg">Tech-Champions</button>
            <NavLink to="contact" onClick={toggleMenu} activeLink={activeLink} offset={offsetValue}>
              <button className="bg-green-600 text-white px-6 py-2 rounded-full text-lg">
                Contact Us
              </button>
            </NavLink>
          </div>
        )}
      </div>
    </nav>
  );
};

const NavLink = ({ to, children, onClick, activeLink, offset }) => {
  return (
    <Link
      to={to}
      smooth={true}
      duration={500}
      spy={true}
      activeClass={activeLink === to ? "font-bold" : ""}
      className={`text-white py-2 px-4 block md:inline-block cursor-pointer font-sans ${
        activeLink === to ? 'font-bold' : ''
      } text-lg md:text-base lg:text-sm`}
      onClick={onClick}
      offset={offset}
    >
      {children}
    </Link>
  );
};

export default Navigation;
