import React, { useState, useEffect, useRef } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Course_1 from '../../assets/Course_7.webp';
import Course_2 from '../../assets/Course_12.webp';
import Course_3 from '../../assets/Course_8.webp';
import Course_4 from '../../assets/Course_9.webp';
import Course_5 from '../../assets/Course_10.webp';
import Course_6 from '../../assets/Course_11.webp';

const Course = () => {
  const courses = [
    {
      id: 1,
      src: Course_1,
      title: 'Scratch Programming',
      description: 'Learn the basics of programming and robotics using Scratch and Pictoblox. This course is perfect for young beginners to start their coding journey with fun and interactive projects.',
    },
    {
      id: 2,
      src: Course_2,
      title: 'App Development',
      description: 'Discover the world of mobile app development. Students will learn to create their own apps and understand the fundamentals of app design and development.',
    },
    {
      id: 3,
      src: Course_3,
      title: 'Python For Kids',
      description: 'Dive into the world of Python programming. This course teaches kids how to code in Python, focusing on developing problem-solving skills through fun and engaging projects.',
    },
    {
      id: 4,
      src: Course_4,
      title: 'Web Development for Kids',
      description: 'Introduce your child to the exciting world of web development. This course teaches kids how to create their own websites, fostering creativity and technical skills through interactive and fun projects.',
    },
    {
      id: 5,
      src: Course_5,
      title: '3D Design for Kids',
      description: 'Unlock your childs creativity with our 3D Design course. Kids will learn to design and model their own 3D creations, developing spatial awareness and technical skills through engaging and imaginative projects.',
    },
    {
      id: 6,
      src: Course_6,
      title: 'AI For Kids',
      description: 'Master advanced AI techniques and applications. This course is for students who are serious about AI and robotics, covering complex topics and hands-on projects.',
    },
  ];

  const [isMobileView, setIsMobileView] = useState(false);
  const sliderRef = useRef(null);

  useEffect(() => {
    const checkScreenWidth = () => {
      setIsMobileView(window.innerWidth < 768);
    };

    checkScreenWidth();
    window.addEventListener('resize', checkScreenWidth);

    return () => window.removeEventListener('resize', checkScreenWidth);
  }, []);

  const sliderSettings = {
    dots: true,
    infinite: true,
    speed: 300,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    pauseOnHover: true,
    pauseOnFocus: true, // Ensures pausing on slide focus for accessibility
    accessibility: true, // Enable accessibility features for the slider
  };

  const handleTouchStart = () => {
    sliderRef.current.slickPause();
  };

  const handleTouchEnd = () => {
    sliderRef.current.slickPlay();
  };

  return (
    <div className="py-12" style={{ fontFamily: 'Poppins, sans-serif' }}>
      <section className="container mx-auto px-6 lg:px-24">
        <h1 className="text-center text-4xl font-bold text-blue-600 mb-8">Coding Courses</h1>

        {isMobileView ? (
          <div
            className="mb-8"
            onTouchStart={handleTouchStart}
            onTouchEnd={handleTouchEnd}
            onMouseDown={handleTouchStart}
            onMouseUp={handleTouchEnd}
          >
            <Slider ref={sliderRef} {...sliderSettings}>
              {courses.map((course) => (
                <div key={course.id} role="tabpanel" aria-label={course.title} className="bg-white shadow-lg rounded-lg overflow-hidden">
                  <div className="relative bg-gray-100 p-4">
                    <img src={course.src} alt={course.title} className="w-30 h-auto mx-auto" />
                  </div>
                  <div className="p-4">
                    <h2 className="text-lg font-bold mb-2">{course.title}</h2>
                    <p className="text-gray-700">{course.description}</p>
                  </div>
                </div>
              ))}
            </Slider>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {courses.map((course) => (
              <div key={course.id} className="bg-white shadow-lg rounded-lg overflow-hidden transform transition-transform hover:scale-105">
                <div className="relative bg-gray-100 p-4">
                  <img src={course.src} alt={course.title} className="w-30 h-auto mx-auto" />
                </div>
                <div className="p-4">
                  <h2 className="text-lg font-bold mb-2">{course.title}</h2>
                  <p className="text-gray-600 mb-4">{course.description}</p>
                </div>
              </div>
            ))}
          </div>
        )}
      </section>
    </div>
  );
};

export default Course;
