import React from 'react';
import Samayaraj from '../../assets/Samayaraj.webp';
import Sathish from '../../assets/Satish.webp';

const EventGuest = () => {
  const guests = [
    {
      name: 'Samayaraj',
      title: 'Co-Founder',
      company: 'Unibose Technology',
      photo: Samayaraj,
      description: 'Samayaraj is the Founder & CEO of Unibose Technology, specializing in advanced robotics and automation. ',
    },
    {
      name: 'Satish',
      title: 'Founder & CEO ',
      company: 'Otomatiks',
      photo: Sathish,
      description: 'Sathish is the Co-Founder of Otomatiks, a leading firm in innovative tech solutions.',

    },
  ];

  return (
    <div className="flex flex-col items-center py-12 px-4 md:px-20 lg:px-32" style={{ fontFamily: 'Poppins, sans-serif' }}>
      <h1 className="text-5xl lg:text-6xl font-bold text-[#202658] text-center mb-12 text-blue-600">Guests Of Honor</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
        {guests.map((guest, index) => (
          <div
            key={index}
            className="relative transform hover:scale-105 hover:shadow-2xl transition-transform duration-500 ease-in-out"
            style={{ backgroundColor: '#FFFFFF', borderRadius: '1rem' }}
          >
            <div className="absolute inset-0 bg-gradient-to-br from-blue-100 to-blue-300 opacity-0 group-hover:opacity-50 transition-opacity duration-500 rounded-lg"></div>
            <img
              src={guest.photo}
              alt={guest.name}
              className="w-40 h-40 rounded-full mx-auto mb-4 transition-transform duration-500 group-hover:scale-110"
            />
            <h2 className="text-3xl font-bold mb-2 text-center text-blue-800">{guest.name}</h2>
            <h3 className="text-lg text-gray-600 mb-2 text-center">
              {guest.title} at <span className="text-blue-600">{guest.company}</span>
            </h3>
            <p className="text-gray-700 text-center">{guest.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default EventGuest;
