import React, { useState, useEffect } from 'react';
import tabletImage from '../../assets/tabletImage.webp'; 
import smartwatchImage from '../../assets/smartwatchImage.webp'; 
import bootcampImage from '../../assets/bootcampImage.webp'; 
import competitionImage from '../../assets/competitionImage.webp'; 

const EventSection = () => {
  const calculateTimeLeft = () => {
    const difference = +new Date(`2024-08-02T23:59:59`) - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
  const [isTimeUp, setIsTimeUp] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      const newTimeLeft = calculateTimeLeft();
      setTimeLeft(newTimeLeft);
      if (Object.keys(newTimeLeft).length === 0) {
        setIsTimeUp(true);
      }
    }, 1000);

    return () => clearTimeout(timer);
  });

  if (isTimeUp) {
    return null; // Hide the entire component when the time is up
  }

  const timerComponents = [];

  Object.keys(timeLeft).forEach((interval) => {
    if (!timeLeft[interval]) {
      return;
    }

    timerComponents.push(
      <span key={interval} className="text-xl md:text-2xl lg:text-3xl font-bold mx-1">
        {timeLeft[interval]} {interval}{" "}
      </span>
    );
  });

  return (
    <div className="flex flex-col items-center py-6 px-4 md:px-20 lg:px-32" style={{ fontFamily: 'Poppins, sans-serif' }}>
      <section className="p-6 md:p-8 mb-8">
        <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold text-blue-600 text-center mb-6">Special Offers</h1>
        <p className="text-lg md:text-xl lg:text-2xl font-bold text-center mb-8">
          Exclusive Bonuses for Spot Admissions
        </p>
        <div className="flex flex-wrap justify-center md:justify-between gap-8">
          <div className="w-full md:w-5/12 p-4">
            <div className="text-center">
              <img className="w-48 h-48 mx-auto mb-4" src={tabletImage} alt="Tablet" />
              <h2 className="text-lg md:text-xl lg:text-2xl font-bold text-indigo-600 mb-4">
                Choose 6 Levels of Robotic / Coding Courses
              </h2>
              <h3 className="text-base md:text-lg font-semibold mb-2">
                Includes a free tablet <br /> Worth Rs 7,500
              </h3>
            </div>
          </div>
          <div className="w-full md:w-5/12 p-4">
            <div className="text-center">
              <img className="w-48 h-48 mx-auto mb-4" src={smartwatchImage} alt="Smartwatch" />
              <h2 className="text-lg md:text-xl lg:text-2xl font-bold text-indigo-600 mb-4">
                Choose 3 Levels of Robotic / Coding Courses
              </h2>
              <h3 className="text-base md:text-lg font-semibold mb-2">
                Includes a free smartwatch <br /> Worth Rs 4,500
              </h3>
            </div>
          </div>
        </div>
      </section>

      <section className="p-6 md:p-8 mb-8">
        <div className="flex flex-wrap justify-center md:justify-between gap-8">
          <div className="w-full md:w-5/12 p-4">
            <div className="text-center">
              <img className="w-48 h-48 mx-auto mb-4" src={competitionImage} alt="Industrial Visit" />
              <h2 className="text-lg md:text-xl lg:text-2xl font-bold text-indigo-600 mb-4">
                Choose 2 Levels of Robotic / Coding Courses
              </h2>
              <h3 className="text-base md:text-lg font-semibold mb-2">
                Complimentary Industrial Visit <br /> Worth Rs 2,000
              </h3>
            </div>
          </div>
          <div className="w-full md:w-5/12 p-4">
            <div className="text-center">
              <img className="w-48 h-48 mx-auto mb-4" src={bootcampImage} alt="Workshop" />
              <h2 className="text-lg md:text-xl lg:text-2xl font-bold text-indigo-600 mb-4">
                Choose 1 Level of Robotic / Coding Courses
              </h2>
              <h3 className="text-base md:text-lg font-semibold mb-2">
                Complimentary Workshop <br /> Worth Rs 1,000
              </h3>
            </div>
          </div>
        </div>
      </section>

      <div className="p-4 md:p-16 text-center bg-gray-100 rounded-lg shadow-lg">
        <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold text-purple-700 mb-4">
          Register before 02nd Aug 2024
        </h2>
        <p className="font-semibold text-lg md:text-xl lg:text-2xl mb-6 text-gray-800">
          To Unlock All Bonuses worth Rs 15,000
        </p>
        <div className="font-semibold text-purple-700 text-xl md:text-2xl lg:text-3xl mb-6">
          {timerComponents.length ? timerComponents : <span>Time's up!</span>}
        </div>
        <a
          href="https://pages.razorpay.com/pl_OOcuhQQsCTdtaA/view"
          className="inline-block bg-green-500 text-white py-3 px-6 rounded-lg text-lg md:text-xl lg:text-2xl font-medium hover:bg-green-600 transition duration-300"
          target="_blank"
          rel="noopener noreferrer"
        >
          Register RoboFest 2024
          <i className="fa fa-chevron-right ml-2" aria-hidden="true"></i>
        </a>
      </div>
    </div>
  );
};

export default EventSection;
