import React, { useState } from 'react';
import Modal from 'react-modal';
import LazyLoad from 'react-lazy-load';

const videos = [
  { title: "DIY Bot Building Workshop", description: "An introductory workshop on the basics of robotics.", link: "https://www.youtube-nocookie.com/embed/kEVcw-qdW0Y" },
  { title: "Robo Race and Drone Workshop", description: "Advanced concepts in robotics and their applications.", link: "https://www.youtube-nocookie.com/embed/J9AmjGMmBwk" },
  { title: "Tech Explorers Summer Camp", description: "Exploring the role of AI in robotics.", link: "https://www.youtube-nocookie.com/embed/jA-MGaD77Cc" },
  { title: "Summer Camp Water Level Indicator", description: "Hands-on session on building robots.", link: "https://www.youtube-nocookie.com/embed/obB83AocPEs" },
  { title: "Project - DIY Bots", description: "Programming techniques for robotics.", link: "https://www.youtube-nocookie.com/embed/halMLKrmjZ0" },
  { title: "Drone Basics and Hands-on", description: "Discussion on the future trends in robotics.", link: "https://www.youtube-nocookie.com/embed/LS1Y6UeSPBs" },
];

const PreviousWorkshopVideos = () => {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [currentVideo, setCurrentVideo] = useState("");

  const openModal = (link) => {
    setCurrentVideo(link);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setCurrentVideo("");
  };

  return (
    <section className="py-8 mx-auto px-6 lg:px-24" style={{ fontFamily: 'Poppins, sans-serif' }}>
      <h2 className="text-4xl font-bold text-center mb-12 text-blue-600">Previous Workshop Videos</h2>
      <div className="flex flex-wrap justify-center">
        {videos.map((video, index) => (
          <div key={index} className="w-full sm:w-1/2 lg:w-1/3 px-6 mb-8 flex items-center justify-center">
            <div className="shadow-lg rounded-lg overflow-hidden w-full" onClick={() => openModal(video.link)}>
              <LazyLoad offsetVertical={300}>
                <iframe
                  width="100%"
                  height="300"
                  src={video.link}
                  title={video.title}
                  frameBorder="0"
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                  allowFullScreen
                ></iframe>
              </LazyLoad>
              <div className="p-4">
                <h3 className="text-xl font-bold mb-2 text-center">{video.title}</h3>
                <p className="text-center">{video.description}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Video Modal"
        className="flex justify-center items-center"
        overlayClassName="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center"
      >
        <div className="bg-white p-4 rounded-lg max-w-4xl w-full">
          <button onClick={closeModal} className="text-right w-full">
            <span className="text-gray-600 text-xl">&times;</span>
          </button>
          <div className="aspect-w-16 aspect-h-9">
            <iframe
              width="300"
              height="300"
              src={currentVideo}
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          </div>
        </div>
      </Modal>
    </section>
  );
};

export default PreviousWorkshopVideos;
